  import VueCoreImageUpload from 'vue-core-image-upload'

export default {
  name: 'uploadimg',
  props: {
    maxImgLength: {
      type: Number,
      default: 3
    }
  },
  data() {
    return {
      imgDataList: [],
      lastImgData: [],
      data: {},
      upIndex: -1,
      upBtnShow: true,
      ceshiImg: ''
    }
  },
  watch: {
    lastImgData: function() {
      console.log('最后的值是：', this.lastImgData)
      this.$emit('getUploadImgArr', this.lastImgData)
    }
  },
  methods: {
    delUploadImg(index) {
      this.imgDataList.splice(index, 1)
      this.lastImgData.splice(index, 1)
      this.upIndex--
      this.changeUpBtnShow()
    },
    imageuploaded(res) {
      console.log(this.maxImgLength, res)
      if (res.state == 'SUCCESS') {
        // this.imgDataList.push('http://api.lxplus.cn' + res.url)
        this.imgDataList.splice(this.upIndex, 1)
        this.imgDataList.push({popShow: true, imgSrc: res.url})
        this.lastImgData.push(res.url)
      }
    },
    imagechanged(res) {
      console.log(res)
      this.upIndex++
      // this.ceshiImg = window.URL.createObjectURL(res)
      this.imgDataList.push({popShow: false, imgSrc: window.URL.createObjectURL(res)})
      this.changeUpBtnShow()
      /* var preview = document.querySelector("img")
      var file = res
      var reader = new FileReader()
      if (file) {
        reader.readAsDataURL(file)
      } else {
        preview.src = ''
      }
      const $this = this
      reader.onloadend = function() {
        console.log(reader.result)
        preview.src = reader.result
        $this.ceshiImg = reader.result
      } */

    },
    changeUpBtnShow() {
      if (this.upIndex >= 2) {
        this.upBtnShow = false
      } else {
        this.upBtnShow = true
      }
    }
  },
  components: {
    'vue-core-image-upload': VueCoreImageUpload
  }
}



