
import Loading from 'vux/src/components/loading/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import tools from '@/common/tools.js'
import axios from 'axios'

export default {
name: 'weixinpay',
data() {
return {
showToast: true,
show2: false,
order_id: ''
}
},
directives: {
TransferDom
},
methods: {
showError(str) {
console.log(str)
},
goToPay(orderId) {


this.order_id = orderId
this.show2 = true
let url = '/Mobile/Payment/getCode.html'
axios.get(url, {
params: {
'pay_radio': 'pay_code=weixin',
'order_id': orderId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.goWeixinPay(res.data.msg)
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
},
goWeixinPay(obj) {
let $this = this
function jsApiCall()
{
WeixinJSBridge.invoke(
'getBrandWCPayRequest',
obj,
function(res){

if(res.err_msg == "get_brand_wcpay_request:ok") {

this.show2 = false
$this.$router.push({'path':'orderdetail', query: {orderId: $this.order_id}})
}else if (res.err_msg == "get_brand_wcpay_request:cancel") {



$this.show2 = false
console.log(res.err_code + res.err_desc + res.err_msg)
} else {
$this.show2 = false

console.log(res.err_code + res.err_desc + res.err_msg)

}
}
);
}

function callpay()
{
if (typeof WeixinJSBridge == "undefined") {
if ( document.addEventListener ) {
document.addEventListener('WeixinJSBridgeReady', jsApiCall, false)
} else if (document.attachEvent) {
document.attachEvent('WeixinJSBridgeReady', jsApiCall)
document.attachEvent('onWeixinJSBridgeReady', jsApiCall)
}
} else {
jsApiCall()
}
}
callpay()
},
},
components: {
Confirm,
XDialog,
Loading
}
}



